@import "../../styles/base/colors";
@import "../../styles/base/variables";

:local {
  $header-height: 60px;

  .Header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.3);
  }

  .HeaderContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: $header-height;
  }

  .NavBlock {
    display: flex;
    height: 100%;
  }

  .MobileNav {
    z-index: 3;
    left: 0;
    right: 0;
    box-shadow: 0 10px 9.5px 0.5px rgba(0, 0, 0, .5);
    border-top: 1px solid #203540;
    position: absolute;
    max-height: calc(100vh - #{$header-height});
    background-color: gainsboro;

    .NavItem {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }

  .MobileNavButton {
    display: inline-block;
    font-size: 16px;
    background-color: transparent;
    border-radius: 2px;
    cursor: pointer;
    border: none;
    padding: 0;
  }

  .MenuIcon {
    width: 25px;
    height: 25px;
  }

  .NavItem {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 24px;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    line-height: 1;
    letter-spacing: 0.8px;
    white-space: nowrap;
    transition: .2s background-color ease-in-out;
    overflow: hidden;
    text-overflow: ellipsis;
    color: col('black');
    width: 100%;

    &:hover {
      background-color: rgba(255, 255, 255, 0.6);
    }

    @media (min-width: $tablet) {
      width: unset;
    }
  }

  .NavItemActive {
    background-color: rgba(255, 255, 255, 0.6);
  }
}